body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f7f7f7;
  display: flex;
  align-items: center; /* Vertikal zentrieren */
  justify-content: center; /* Horizontal zentrieren */
  height: 100vh;
}

.container {
  width: 400px; 
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column; 
  align-items: center;
}

h1 {
  text-align: center;
  color: #333;
}

.select-label {
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 1.2em;
}

.select-box,
.textarea-input {
  width: 100%; /* Vollständige Breite */
  padding: 8px;
  margin-top: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #fff;
  font-size: 16px;
}

.action-buttons {
  margin-top: 10px;
  display: flex; /* Neue Linie für Buttons */
  justify-content: center; /* Zentrierte Buttons */
}

.synthesize-button,
.download-button {
  padding: 10px 15px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
  background-color: #f44336; 
  color: #fff;
}

.synthesize-button:hover,
.download-button:hover {
  background-color: #d32f2f; 
}

.audio-player {
  margin-top: 10px;
}

.voice-section {
  margin-bottom: 20px; 
}

.voice-section button {
  margin-top: 10px;
  padding: 8px 12px;
  background-color: #f44336; 
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.voice-section button:hover {
  background-color: #d32f2f; 
}
